.--civil-engineering {
    background: url("../images/civilengineering/CivilEng_Shapes-01.png") top 10vw left -8vw,
    url("../images/civilengineering/CivilEng_Shapes-02.png") top 77vw left -2.5vw,
    url("../images/civilengineering/CivilEng_Shapes-03.png") top 297vw left -2vw,
    url("../images/civilengineering/CivilEng_Shapes-04.png") bottom 60vw right -32vw,
    url("../images/civilengineering/CivilEng_Shapes-05.png") bottom -2vw left,
    url("../images/civilengineering/CivilEng_BG-01.png") bottom -2.8vw right -1vw;
    background-size: 145vw auto, 35vw auto, 105vw auto, 175vw auto, 53vw auto, 102vw auto;
    background-repeat: no-repeat, no-repeat , no-repeat, no-repeat, no-repeat, no-repeat;
    background-attachment: scroll;
    transition: background 0.3s;
    image-rendering: -webkit-optimize-contrast;
    .--bg-1 {
        padding: 10vw 0 5vw 0;
        min-height: 51vw;
        margin-top: -9.1vw;
        width: 100%;
        .--title {
            font-family: "gill sans",sans-serif !important;
            letter-spacing: 0.4vw;
            font-weight: 600 !important;
            text-transform: uppercase;
            line-height: 3.1vw;
            font-size: 2vw;
            color: #283a97;
            margin: 16.1vw 0 2vw 0;
            text-align: center;
        }
        .--desc {
            font-family: "gill sans",sans-serif;
            font-weight: 600 !important;
            margin-bottom: 9.4vw;
            font-size: 1.6vw;
            color: #676ab0;
            line-height: 2.3vw;
            text-align: center;
            letter-spacing: 0.25vw;
        }
    }
    .--bg-2 {
        padding: 10vw 0 5vw 0;
        min-height: 51vw;
        width: 100%;
        .--title {
            font-family: "gill sans",sans-serif !important;
            letter-spacing: 0.4vw;
            font-weight: 600 !important;
            text-transform: uppercase;
            line-height: 3.1vw;
            font-size: 2vw;
            color: #283a97;
            margin: 5.5vw 0 2vw 0;
            text-align: center;
        }
        hr {
            background-color: #283997;
            height: 2px;
            width: 30vw;
            opacity: 1;
            margin: 1vw auto 1.5vw auto;
        }
        hr:not([size]) {
            height: 2px;
        }
        .--desc {
            font-family: "gill sans",sans-serif;
            font-weight: 600 !important;
            margin-bottom: 9.4vw;
            font-size: 1.6vw;
            color: #676ab0;
            line-height: 2.3vw;
            text-align: center;
            letter-spacing: 0.25vw;
        }
    }
    .--bg-3 {
        padding: 15vw 0 5vw 0;
        min-height: 51vw;
        width: 100%;
        .--max {
            max-width: 75%;
        }
        .card {
            width: 79%;
            border-radius: 0 !important;
            background-color: transparent !important;
            border: 0 !important;
            margin-bottom: 7vw;
            &.--blue {
                .card-body {
                    .card-title,
                    .card-text {
                        color: #ffffff;
                    }
                }
            }
            &.--right {
                margin-left: auto;
            }
            img {
                width: 28.4vw;
                height: 28.4vw;
                object-position: 0 0;
                object-fit: contain;
                image-rendering: pixelated;
                border-radius:  0 !important;
            }
            .card-body {
                padding: 1.5vw 0;
                .card-title {
                    font-family: "gill sans",sans-serif !important;
                    letter-spacing: 0.24vw;
                    font-weight: 600 !important;
                    text-transform: uppercase;
                    line-height: normal;
                    font-size: 1vw;
                    color: #000000;
                    text-align: left;
                    margin-bottom: 30px;
                }
                .card-text {
                    letter-spacing: 0.2vw;
                    color: #000000;
                    text-align: left;
                    margin-bottom: 35px;
                    span.--title {
                        font-family: "gill sans ",sans-serif !important;
                        font-weight: 600 !important;
                        line-height: normal;
                        font-size: 0.9vw;
                    }
                    span {
                        display: block; 
                        font-family: "gill sans light",sans-serif !important;
                        font-weight: 300 !important;
                        line-height: normal;
                        font-size: 1vw;
                    }
                }
                .--btn-view-project {
                    background-color: #ffffff !important;
                    border: 0 !important;
                    border-radius: 0 !important;
                    font-family: "gill sans",sans-serif !important;
                    letter-spacing: 0.1vw;
                    color: #283997;
                    text-align: center;
                    text-transform: uppercase;
                    padding: 15px 3.3vw;
                    box-shadow: none !important;
                    outline: 0 !important;
                    text-decoration: none !important;
                    border: 0 !important;
                }
            }
        }
    }
    .--bg-4 {
        padding: 8vw 0 5vw 0;
        min-height: 51vw;
        width: 100%;
        .--max {
            max-width: 70%;
        }
        .--white {
            .card {
                .card-body {
                    .card-title,
                    .card-text {
                        color: #ffffff !important;
                    }
                }
            }
        }
        .card {
            max-width: 85%;
            border-radius: 0 !important;
            background-color: transparent !important;
            border: 0 !important;
            margin: 0 0 2vw auto;
            &.--blue {
                .card-body {
                    .card-title,
                    .card-text {
                        color: #ffffff;
                    }
                }
            }
            &.--right {
                margin-left: auto;
            }
            img {
                width: 10vw;
                height: 10vw;
                object-position: 0 0;
                object-fit: contain;
                image-rendering: -webkit-optimize-contrast;
                border-radius: 0 !important;
                margin: 0 auto 0 5vw;
            }
            .card-body {
                padding: 3vw 0;
                .card-title {
                    font-family: "gill sans",sans-serif !important;
                    letter-spacing: 0.2vw;
                    font-weight: 600 !important;
                    text-transform: uppercase;
                    line-height: normal;
                    font-size: 1vw;
                    color: #000000;
                    text-align: left;
                    margin-bottom: 30px;
                    min-height: 5.5vw;
                }
                .card-text {
                    letter-spacing: 0.2vw;
                    color: #000000;
                    text-align: left;
                    margin-bottom: 35px;
                    span.--title {
                        font-family: "gill sans ",sans-serif !important;
                        font-weight: 600 !important;
                        line-height: normal;
                        font-size: 0.9vw;
                    }
                    span {
                        display: block; 
                        font-family: "gill sans light",sans-serif !important;
                        font-weight: 300 !important;
                        line-height: normal;
                        font-size: 1vw;
                    }
                }
                .--btn-view-project {
                    background-color: #ffffff !important;
                    border: 0 !important;
                    border-radius: 0 !important;
                    font-family: "gill sans",sans-serif !important;
                    letter-spacing: 0.1vw;
                    color: #283997;
                    text-align: center;
                    text-transform: uppercase;
                    padding: 15px 3.3vw;
                }
            }
        }
    }
    @media (max-width: 992px) {
        background: url("../images/mobile/licenses/Licenses_BG-03.png") top -50vw center,
        url("../images/mobile/civilengineering/CivilEng_BG-01.png") top 135vw center;;
        background-size: 100vw auto, 115vw auto;
        background-repeat: no-repeat, no-repeat;
        background-attachment: scroll, scroll;
        transition: background 0.3s;
        image-rendering: -webkit-optimize-contrast;
        .--bg-1 {
            margin-top: 0;
            padding: 160px 0 100px 0;
            h1 {
                &.--title {
                    font-size: 1.500em;
                    margin-bottom: 25px;
                }
            }
            p {
                &.--desc {
                    font-size: 1.000em;
                    line-height: 1.6;
                }
            }
        }
        .--bg-2 {
            padding: 100px 0;
            h1 {
                &.--title {
                    font-size: 1.500em;
                    margin-bottom: 25px;
                }
            }
            p {
                &.--desc {
                    font-size: 1.000em;
                    line-height: 1.6;
                }
            }
        }
        .--bg-3 {
            padding: 100px 0;
            .--max {
                max-width: 90%;
                .col-md-6 {
                    width: 50%;
                    max-width: 50%;
                }
            }
            .card {
                width: 90%; 
                margin-bottom: 100px;
                img {
                    width: auto;
                    height: 38vw;
                }
                .card-body {
                    .card-title {
                        font-size: 1.000em;
                        br {
                            display: none;
                        }
                    }
                    .card-text {
                        span.--title {
                            font-size:0.875em;
                        }
                        span {
                            font-size:0.875em;
                        }
                    }
                    .--btn-view-project {
                        font-size:0.875em;
                    }
                }
            }
        }
        .--bg-4 {
            .--max {
                max-width: 90%;
                .col-md-6 {
                    width: 50%;
                    max-width: 50%;
                }
            }
            .card {
                img {
                    width: 18vw;
                    height: 18vw;
                }
                .card-body {
                    .card-title {
                        font-size:0.875em;
                        br {
                            display: none;
                        }
                    }
                    .card-text {
                        span.--title {
                            font-size: 0.875em;
                        }
                        span {
                            font-size:0.875em;
                        }
                    }
                }
            }
        }
    }
    @media (max-width: 835px) {
        background: url("../images/mobile/licenses/Licenses_BG-03.png") top -34vw center,
        url("../images/mobile/civilengineering/CivilEng_BG-01.png") top 154vw center;;
        background-size: 100vw auto, 200vw auto;
        background-repeat: no-repeat, no-repeat;
        background-attachment: scroll, scroll;
        transition: background 0.3s;
        image-rendering: -webkit-optimize-contrast;
    }
    @media (max-width: 700px) {
        background: url("../images/mobile/licenses/Licenses_BG-03.png") top -15vw center,
        url("../images/mobile/civilengineering/CivilEng_BG-01.png") top 145vw center;;
        background-size: 100vw auto, 225vw auto !important;
        background-repeat: no-repeat, no-repeat;
        background-attachment: scroll, scroll;
        transition: background 0.3s;
        image-rendering: -webkit-optimize-contrast;
        .--bg-1 {
            padding: 18vw 0 10vw 0;
        }
        .--bg-2 {
            padding: 10vw 0;
        }
        .--bg-3 {
            padding: 27vw 0 10vw 0;
        }
    }
    @media (max-width: 500px) {
        background: url("../images/mobile/licenses/Licenses_BG-03.png") top -15vw center,
        url("../images/mobile/civilengineering/CivilEng_BG-01.png") top 172vw center;;
        background-size: 100vw auto, 250vw auto !important;
        background-repeat: no-repeat, no-repeat;
        background-attachment: scroll, scroll;
        transition: background 0.3s;
        image-rendering: -webkit-optimize-contrast;
        .--bg-3 {
            padding: 27vw 0 10vw 0;
        }
    }
    @media (max-width:420px) {
        background: url("../images/mobile/licenses/Licenses_BG-03.png") top -15vw center,
        url("../images/mobile/civilengineering/CivilEng_BG-01.png") top 165vw center;;
        background-size: 110vw auto, 220vw auto !important;
        background-repeat: no-repeat, no-repeat;
        background-attachment: scroll, scroll;
        transition: background 0.3s;
        image-rendering: -webkit-optimize-contrast;
    }
}