.--awards-and-achievements {
    background: url("../images/awardsandachievements/Awards_Shapes-01.png") top 11vw left -24vw,
    url("../images/awardsandachievements/Awards_Shapes-02.png") top 110vw left -25vw, 
    url("../images/awardsandachievements/Awards_BG-02.png") top 52vw right -2vw,
    url("../images/awardsandachievements/Awards_BG-03.png") bottom 0 center,
    url("../images/awardsandachievements/Awards_BG-01.png") top -7vw right;
    background-size: 156vw auto, 62vw 33vw, 102vw auto, 100vw auto, 124vw auto;
    background-repeat: no-repeat, no-repeat, no-repeat, no-repeat, no-repeat;
    background-attachment: scroll;
    transition: background 0.3s;
    .--bg-1 {
        min-height: 50vw;
        margin-top: -9.1vw;
        width: 100%;
        padding: 10vw 0 5vw 0;
        .--title {
            font-family: "gill sans",sans-serif !important;
            letter-spacing: 0.5vw;
            font-weight: 600 !important;
            text-transform: uppercase;
            line-height: 3.1vw;
            font-size: 2.3vw;
            color: #283a97;
            margin: 10.3vw 0 3.4vw 0;
            text-align: center;
        }
        .--desc {
            font-family: "gill sans medium",sans-serif;
            font-weight: 500 !important;
            margin-bottom: 9.4vw;
            font-size: 1vw;
            color: #676ab0;
            line-height: 1.9vw;
            text-align: center;
            text-transform: uppercase;
        }
    }
    .--bg-2 {
        width: 100%;
        padding-top: 2vw;
        height: 90vw;
        .--max {
            max-width: 75%;
        }
        .--awards {
            width: 90%;
            height: auto;
            object-fit: contain;
            object-position: 0 0;
            image-rendering: pixelated;
        }
        .--text-container {
            display: flex;
            justify-content: flex-end;
            align-items: flex-start;
            flex-direction: column;
            height: 100%;
            position: relative;
            right: unset;
            left: unset;
            bottom: 0;
            .--title {
                font-family: "gill sans",sans-serif !important;
                letter-spacing: 0.4vw;
                font-weight: 600 !important;
                text-transform: uppercase;
                line-height: 3.1vw;
                font-size: 1.4vw;
                color: #ffffff;
                margin: 0 0 1.8vw 0;
                text-align: left;
            }
            .--desc {
                font-family: "gill sans",sans-serif;
                font-weight: 500 !important;
                margin-bottom: 0;
                letter-spacing: 0.2vw;
                font-size: 0.9vw;
                color: #ffffff;
                line-height: 2.5vw;
                text-align: left;
                text-transform: uppercase;
            }
        }
        .carousel-control-prev,
        .carousel-control-next {
            display: none !important;
        }
        .carousel-inner {
            height: 50vw;
        }
        .carousel-indicators {
            bottom: -12vw;
            li {
                height: 30px;
                width: 30px;
                border-radius: 100px;
                border: 1px solid #ffffff;
                background-color: transparent;
                transition: background-color 0.5s;
                margin: 0 2vw;
                :hover {
                    background-color: #ffffff;
                    transition: background-color 0.5s;
                }
                &.active {
                    background-color: #ffffff;
                    transition: background-color 0.5s;
                }
            }
        }
    }
    .--bg-3 {
        width: 100%;
        padding: 8.5vw 0 10vw 0;
        height: 105vw;
        background: url("../images/awardsandachievements/Awards_Shapes-03.png") bottom 0 center;
        background-size: 100vw auto;
        background-repeat: no-repeat;
        background-attachment: scroll;
        transition: background 0.3s;
        .--max {
            max-width: 73%;
        }
        .--title {
            font-family: "gill sans",sans-serif !important;
            letter-spacing: 0.5vw;
            font-weight: 600 !important;
            text-transform: uppercase;
            line-height: 3.1vw;
            font-size: 2.3vw;
            color: #283a97;
            margin: 0vw 0 11.4vw 0;
            text-align: center;
        }
        .--testimonial {
            width: 95%;
            height: auto;
            object-fit: contain;
            object-position: 0 0;
            image-rendering: pixelated;
        }
        .--text-container {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            flex-direction: column;
            height: 100%;
            position: relative;
            right: unset;
            left: unset;
            bottom: 0;
            padding-top: 0;
            .--title {
                font-family: "gill sans",sans-serif !important;
                letter-spacing: 0.2vw;
                font-weight: 600 !important;
                text-transform: uppercase;
                line-height: 2.5vw;
                font-size: 1.7vw;
                color: #283997;
                margin: 10.5vw 0 3vw 0;
                text-align: center;
            }
            hr {
                background-color: #283997;
                height: 7vw;
                width: 2px;
                opacity: 1;
            }
            .--desc {
                font-family: "gill sans",sans-serif;
                font-weight: 500 !important;
                margin-bottom: 0;
                margin-top: 1vw;
                letter-spacing: 0.2vw;
                font-size: 1vw;
                color: #283997;
                line-height: 2.5vw;
                text-align: center;
                text-transform: uppercase;
            }
        }
    }
    @media (max-width: 992px) {
        background: url("../images/awardsandachievements/Awards_Shapes-01.png") top 11vw left -24vw,
        url("../images/awardsandachievements/Awards_Shapes-02.png") top 110vw left -25vw, 
        url("../images/mobile/awardsandachievements/Awards_BG-01.png") top 43vw center,
        // url("../images/awardsandachievements/Awards_BG-01.png") bottom 0 center,
        url("../images/awardsandachievements/Awards_BG-01.png") top -7vw right;
        background-size: 156vw auto, 62vw 33vw, 100vw 135vw ,auto 75vw;
        background-repeat: no-repeat, no-repeat, no-repeat, no-repeat, no-repeat;
        background-attachment: scroll;
        transition: background 0.3s;
        .--bg-1 {
            padding: 100px 0;
            margin: 0 auto;
            max-width: 90%;
            min-height: 50vw;
            .--title {
                font-size: 	1.500em;
                line-height: 1.6;
                br {
                    display: none;
                }
            }
            .--desc {
                font-size: 	1.000em;
                line-height: 1.6;
                br {
                    display: none;
                }
            }
        }
        .--bg-2 {
            min-height: 100vw;
            padding-bottom: 100px;
            height: auto;
            .col-md-7,
            .col {
                flex: 0 0 100%;
                width: 100%;
            }
            .--max {
                max-width: 90%;
            }
            .--awards {
                margin: 0 auto;
                display: block;
                width: 65%;
            }
            .carousel-inner {
                min-height: 90vw;
                height: auto;
            }
            .--text-container {
                justify-content: center;
                align-items: center;
                h6 {
                    &.--title {
                        font-size: 	1.125em;
                        line-height: 1.6;
                        text-align: center;
                    }
                }
                p {
                    &.--desc {
                        font-size: 	0.875em;
                        line-height: 1.6;
                        text-align: center;
                        br {
                            display: none;
                        }
                    }
                }
            }
            .carousel-indicators {
                li {
                    height: 23px;
                    width: 23px;
                    margin: 0 5vw;
                }
            }
        }
        .--bg-3 {
            padding: 200px 0 100px 0;
            height: auto;
            min-height: 50vw;
            position: relative;
            &::before {
                position: absolute;
                top: -4vw;
                right: 0;
                bottom: 0;
                left: 0;
                content: "";
                background: url("../images/mobile/awardsandachievements/Awards_BG-02.png") top center;
                background-size: 100vw auto;
                background-repeat: no-repeat;
                background-attachment: scroll;
                z-index: -1;
                width: 100%;
            }
            .col-md-7,
            .col {
                flex: 0 0 100%;
                width: 100%;
            }
            .--max {
                max-width: 90%;
            }
            h1 {
                &.--title {
                    font-size: 	1.500em;
                }
            }
            .--text-container {
                h6 {
                    &.--title {
                        font-size: 1.375em;
                        line-height: 1.6;
                    }
                }
                p {
                    &.--desc {
                        font-size: 	0.875em;
                        line-height: 1.6;
                    }
                }
            }
            .--testimonial {
                width: 65%;
                height: auto;
                object-fit: contain;
                object-position: 0 0;
                image-rendering: pixelated;
                display: block;
                margin: 0 auto;
            }
        }
    }
    @media (max-width: 920px) {
        .--bg-3 {
            &::before {
                top: -10vw;
            }
        }
    }
    @media (max-width: 860px) {
        .--bg-3 {
            &::before {
                top: -15vw;
            }
        }
    }
    @media (max-width:796px) {
        background: url("../images/awardsandachievements/Awards_Shapes-01.png") top 11vw left -24vw,
        url("../images/awardsandachievements/Awards_Shapes-02.png") top 110vw left -25vw, 
        url("../images/mobile/awardsandachievements/Awards_BG-01.png") top 53vw right -2vw,
        url("../images/awardsandachievements/Awards_BG-03.png") bottom 0 center,
        url("../images/awardsandachievements/Awards_BG-01.png") top -7vw right;
        background-size: 156vw auto, 62vw 33vw, 100vw 170vw, 100vw auto,auto 90vw;
        background-repeat: no-repeat, no-repeat, no-repeat, no-repeat, no-repeat;
        background-attachment: scroll;
        transition: background 0.3s;
        .--bg-2 {
            min-height: 145vw;
        }
    }
    @media (max-width:796px) {
        .--bg-2 {
            min-height: 132vw;
        }
    }
    @media (max-width:680px) {
        .--bg-3 {
            padding: 140px 0 100px 0;
            &::before {
                top: -20vw;
            }
        }

    }
    @media (max-width:600px) {
        background: url("../images/awardsandachievements/Awards_Shapes-01.png") top 11vw left -24vw,
        url("../images/awardsandachievements/Awards_Shapes-02.png") top 110vw left -25vw, 
        url("../images/mobile/awardsandachievements/Awards_BG-01.png") top 65vw right -2vw,
        url("../images/awardsandachievements/Awards_BG-03.png") bottom 0 center,
        url("../images/awardsandachievements/Awards_BG-01.png") top -7vw right;
        background-size: 156vw auto, 62vw 33vw, 100vw 160vw, 100vw auto,auto 105vw;
        background-repeat: no-repeat, no-repeat, no-repeat, no-repeat, no-repeat;
        background-attachment: scroll;
        transition: background 0.3s;
    }
    @media (max-width:569px) {
        background: url("../images/awardsandachievements/Awards_Shapes-01.png") top 24vw left -24vw,
        url("../images/awardsandachievements/Awards_Shapes-02.png") top 110vw left -25vw, 
        url("../images/mobile/awardsandachievements/Awards_BG-01.png") top 90vw right -2vw,
        url("../images/awardsandachievements/Awards_BG-03.png") bottom 0 center,
        url("../images/awardsandachievements/Awards_BG-01.png") top -7vw right;
        background-size: 156vw auto, 62vw 33vw, 140vw 135vw, 100vw auto,auto 135vw;
        background-repeat: no-repeat, no-repeat, no-repeat, no-repeat, no-repeat;
        background-attachment: scroll;
        transition: background 0.3s;
    }
    @media (max-width:500px) {
        background: url("../images/awardsandachievements/Awards_Shapes-01.png") top 24vw left -24vw,
        url("../images/awardsandachievements/Awards_Shapes-02.png") top 110vw left -25vw, 
        url("../images/mobile/awardsandachievements/Awards_BG-01.png") top 90vw right -2vw,
        url("../images/awardsandachievements/Awards_BG-03.png") bottom 0 center,
        url("../images/awardsandachievements/Awards_BG-01.png") top -7vw right;
        background-size: 156vw auto, 62vw 33vw, 140vw 190vw, 100vw auto,auto 135vw;
        background-repeat: no-repeat, no-repeat, no-repeat, no-repeat, no-repeat;
        background-attachment: scroll;
        transition: background 0.3s;
    }
}